@import "src/styles/global.scss"; @import "portal/global.scss";



























@include themeWrap {
    .demo-row-item {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-bottom: 12px;
        .demo-row-item-title {
            font-size: 14px;
            width: 100px;
            color: plVar(colorTitle);
        }
        .demo-row-item-content {
            flex: 1;
            & > * {
                margin-right: 12px;
            }
        }
    }
}
