@import "src/styles/global.scss"; @import "portal/global.scss";















































































































@include themeWrap {
    .pl-base-table-cell-watcher {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        outline: none;
        padding: 1px;

        &.pl-base-table-cell-watcher-link {
            color: plVar(colorPrimary);
            cursor: pointer;
        }
    }
}
