@import "src/styles/global.scss"; @import "portal/global.scss";






































html, body {
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

@include themeWrap {
    .app {
        height: 100%;
        width: 100%;
        background-size: 100% 100%;
        display: flex;
        flex-direction: column;

        .app-header {
            height: 64px;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: space-between;
            box-sizing: border-box;
            padding: 0 plVar(padding);
            border-bottom: 1px solid #ddd;

            .logo {
                height: 80%;
                width: auto;
            }
        }

        .app-body {
            flex: 1;
            display: flex;
            position: relative;
            overflow: hidden;

            .app-left {
                width: 280px;
                height: 100%;
                position: relative;

                &:before {
                    position: absolute;
                    right: 0;
                    width: 3px;
                    top: 0;
                    bottom: 0;
                    content: '';
                    box-shadow: 0 0 12px #ddd;
                }

                &:after {
                    position: absolute;
                    right: 1px;
                    width: 10px;
                    top: 0;
                    bottom: 0;
                    content: '';
                    background-color: white;
                }
            }

            .app-right {
                flex: 1;
                overflow: hidden;
            }
        }
    }
}
