@import "src/styles/global.scss"; @import "portal/global.scss";

















































































































.pl-base-table-cell {
    display: inline-flex;
    box-sizing: border-box;
    padding: 0 6px;

    .pl-base-table-cell-content {
        flex: 1;
        display: flex;
        align-items: center;
        overflow: hidden;
        text-overflow: ellipsis;

        .pl-base-table-cell-watcher {
            flex: 1;

            .pl-render-func, .pl-scope-slot {
                width: 100%;
            }
        }
    }

    .pl-base-table-cell-content-slot {
        display: flex;
        align-items: center;
    }

    .pl-input, .pl-date, .pl-time  {
        width: 100% !important;
    }

    &.pl-base-table-cell-align-left {
        .pl-base-table-cell-content {
            justify-content: flex-start;

            .pl-base-table-cell-watcher {
                text-align: left;
            }
        }
    }

    &.pl-base-table-cell-align-center {
        .pl-base-table-cell-content {
            justify-content: center;

            .pl-base-table-cell-watcher {
                text-align: center;
            }
        }
    }

    &.pl-base-table-cell-align-right {
        flex-direction: row-reverse;

        .pl-base-table-cell-content {
            justify-content: flex-end;

            .pl-base-table-cell-watcher {
                text-align: right;
            }
        }
    }


}

.pl-base-table-body {
    .pl-base-table-row:not(:last-child) {
        .pl-base-table-cell {
            border-bottom: solid 1px #f2f2f2;
            box-sizing: border-box;
        }
    }
}
