@import "src/styles/global.scss"; @import "portal/global.scss";























@include themeWrap {
    .demo-row {
        @include public-style;
        width: 100%;
        box-sizing: border-box;
        .demo-row-content {
            background-color: plVar(backgroundColorDeeper);
            width: 100%;
            height: max-content;
            border-radius: plVar(borderFillet);
            padding: plVar(paddingLarge);
            box-sizing: border-box;
        }
        .demo-row-title {
            display: flex;
            align-items: center;
            color: plVar(colorPrimaryDeep);
            border-bottom: solid 1px plVar(backgroundColor);
            padding-bottom: plVar(padding);
            font-weight: bold;
            span {
                margin-right: 12px;
            }
        }
        .demo-row-body {
            & > *:not(:last-child) {
                margin-right: 12px;
            }
        }
    }
}
